@-webkit-keyframes lineAnim {
    to{
        stroke-dashoffset: 0;
    }
}

@keyframes lineAnim {
    to{
        stroke-dashoffset: 0;
    }
}

@-webkit-keyframes fillAnim{
    from{
        fill: transparent;
    }
    to{
        fill: rgb(255, 255, 255);
    }
}

@keyframes fillAnim{
    from{
        fill: transparent;
    }
    to{
        fill: rgb(255, 255, 255);
    }
}

@-webkit-keyframes showPage {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes showPage {
    from{
        opacity: 0;
    }
    to{
