:root{
    --iconColor: var(--light);
}

.button{
    all: unset;
    text-transform: none;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
    padding: .5vw;
    font-size: 1.2rem;
    cursor: pointer;
    color: var(--pDark);
    background: transparent;
    border-radius: 0;
}

.header.button{
    padding: 1vw;
}

.leftLine {
    border: none;
    border-left: 5px solid var(--pDark);
}

.rightLine {
    border: none;
    border-right: 5px solid var(--pDark);
}

.bottomLine {
    border: none;
    border-bottom: 5px solid var(--pDark);
}

.topLine {
    border: none;
    border-top: 5px solid var(--pDark);
}

:root .button{
    --iconColor: var(--pDark);
}

.invert{
    color: var(--light);
}

.invert.leftLine {
    border: none;
    border-left: 5px solid var(--light);
}

.invert.rightLine {
    border: none;
    border-right: 5px solid var(--light);
}

.invert.bottomLine {
    border: none;
    border-bottom: 5px solid var(--light);
}

.invert.topLine {
    border: none;
    border-top: 5px solid var(--light);
}


:root .invert{
    --iconColor: var(--light);
}

.button span {
    z-index: 3;
    transition: .5s;
}

.button *{
    z-index: 3;
    transition: .5s;
}

.button:hover, .button:focus span{
color: var(--light);
}

.button:hover, .button:focus {
    --iconColor: var(--light);
}

.button:hover, .button:focus{
    background: transparent;
}

.invert:hover, .invert:focus span{
    color: var(--pDark);
}

.invert:hover, .invert:focus {
    --iconColor: var(--pDark);
}

.leftLine.button:hover:before, .leftLine.button:focus:before, .leftLine.invert:hover:before, .leftLine.invert:focus:before{
    right:0;
}

.rightLine.button:hover:before, .rightLine.button:focus:before, .rightLine.invert:hover:before, .rightLine.invert:focus:before{
    left:0;
}

.topLine.button:hover:before, .topLine.button:focus:before, .topLine.invert:hover:before, .topLine.invert:focus:before{
    bottom:0;
}

.bottomLine.button:hover:before, .bottomLine.button:focus:before, .bottomLine.invert:hover:before, .bottomLine.invert:focus:before{
    top:0;
}

.button:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: var(--pDark);
    transition: .5s;
    z-index: 2;
}

.invert:before {
    background: var(--light);
}

.leftLine.button:before{
    right: 100%;
}

.rightLine.button:before{
    left: 100%;
}

.topLine.button:before{
    bottom: 100%;
}

.bottomLine.button:before{
    top: 100%;
}

